.navigation {
  grid-column: 1/2;
  grid-row: 2/3;
  background-color: antiquewhite;
  display: block;
  height: 100%;
}
a {
  color: #2e2e2e;
  text-decoration: none;
}
.item {
  font-size: 16px;
}
.item a.active {
  color: #3a33d1;
  font-size: 40px;
}
.item a.activeLink {
  color: #3a33d1;
  font-size: 40px;
}
