.wrapper_info {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  width: 80%;
  border-radius: 10px;
  margin-left: 10px;
  padding: 10px;
}
.follow {
  display: block;
  height: 30px;
  width: 100px;
  border: 1px solid #f91a16;
  cursor: pointer;
}
.gogo {
  background: #5e740b;
  height: 100px;
  width: 100px;
  display: block;
}
.foto {
  height: 50px;
  width: 50px;
}
.user_info {
  display: flex;
  margin-top: 5px;
  padding: 10px;
}
.selectPage {
  color: red;
  font-size: 25px;
  cursor: pointer;
}
span {
  margin-right: 20px;
  cursor: pointer;
}
