body {
  background-color: coral;
}
.grid_b {
  border: 1px solid black;
}
.wrapper {
  display: grid;
  grid-template-columns: minmax(400px, auto) 1fr;
  grid-template-rows: 70px 1fr;
  grid-gap: 10px;
  height: 100vh;
  max-width: 1500px;
  width: 100%;
  margin: auto;
  padding: 10px;
}
.wrapper .app_wrapper_content {
  grid-column: 2/3;
  grid-row: 2/3;
  background-color: aquamarine;
  display: block;
}
